<template>
  <div class="input-wrapper">
    <div class="labels">
      <div v-for="(item, index) in checkboxType" :key="index">
        <input type="checkbox" v-model="item.selected" :id="item.figures" />
        <label :for="item.figures">{{ $t(`${item.label}`) }}</label>
      </div>
    </div>
    <textarea
      name="userInputBet"
      id="userInputBet"
      @input="checkUserInputBet"
      v-model="userInputBet"
      cols="30"
      rows="5"
    >
    </textarea>
    <div class="random-numbers clear-fix top-more">
      <a
        v-for="(randomItem, randomIndex) in randomInputNumbers"
        @click="randomInputCounts(randomItem.value)"
        :key="randomIndex"
        href="javascript:;"
        >{{ $t(randomItem.label) }}</a
      >
    </div>
    <div class="clear-fix">
      <p>{{ $t("gameTemplate.tips1") }}</p>
      <a @click="clearUserInputBet" href="javascript:;">{{
        $t("gameTemplate.empty")
      }}</a>
      <a @click="filterUserInput" href="javascript:;">{{
        $t("gameTemplate.filter")
      }}</a>
    </div>
  </div>
</template>

<script>
import { SET_SSC_SELECTED_COUNTS } from "../../../store/nameSpace";
import inputReset from "../../credit/mixins/input-reset";
import minxinsLrylBtnControl from "../../credit/minxins-lrylbtncontrolfalse";
import { checkLotteryCounts } from "../../../common/js/LotteryBet";
import {
  checkboxTypeLabelValue,
  randomInputNumbers
} from "../../../config/game-default-config";

export default {
  mixins: [inputReset, minxinsLrylBtnControl],
  props: [
    "flagCount",
    "getShoppingCarList",
    "nowPlayWayInfo",
    "nowActiveWayInfoDetail"
  ],
  data() {
    return {
      randomInputNumbers,
      userInputBet: "",
      selectedCounts: 0,
      checkboxType: [
        {
          figures: "wan",
          label: "gameTemplate.thousandPosition",
          selected: true
        },
        {
          figures: "qian",
          label: "gameTemplate.ThousandBit",
          selected: true
        },
        {
          figures: "bai",
          label: "gameTemplate.HundredBit",
          selected: true
        },
        {
          figures: "shi",
          label: "gameTemplate.TenPlace",
          selected: true
        },
        {
          figures: "ge",
          label: "gameTemplate.Bit",
          selected: true
        }
      ]
    };
  },
  methods: {
    filterUserInput() {
      if (!this.userInputBet) return;
      let renxuanCounts = 0;
      for (let i = 0; i < this.checkboxType.length; i++) {
        if (this.checkboxType[i].selected === true) {
          renxuanCounts = renxuanCounts + 1;
        }
      }
      const result = checkLotteryCounts({
        betData: this.userInputBet,
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"],
        renxuanCounts: renxuanCounts
      });
      if (!result.sourceData.length) {
        this.userInputBet = "";
        return;
      }
      let areaSplitChar =
        this.nowActiveWayInfoDetail["area_split_char"] === "j"
          ? ";"
          : this.nowActiveWayInfoDetail["area_split_char"];
      let ballSplitChar =
        this.nowActiveWayInfoDetail["ball_split_char"] === "j"
          ? ";"
          : this.nowActiveWayInfoDetail["ball_split_char"];
      if (this.nowActiveGameName === "GGBALOTO") {
        this.userInputBet =
          result.sourceData
            .map(item => {
              return (
                item.slice(0, item.length - 1).join(ballSplitChar) +
                "+" +
                item[item.length - 1]
              );
            })
            .join(areaSplitChar) + areaSplitChar;
      } else {
        this.userInputBet =
          result.sourceData
            .map(item => item.join(ballSplitChar))
            .join(areaSplitChar) + areaSplitChar;
      }
      this.$message({
        message: "号码已按照标准格式进行重置",
        type: "success",
        duration: 1000
      });
    },
    getExtraStr() {
      let extraStr = "";
      this.checkboxType.map(item => {
        if (item.selected) {
          extraStr += checkboxTypeLabelValue[item.figures];
        }
      });
      return extraStr
        .split("")
        .map(item => +item)
        .sort((a, b) => a - b)
        .join("");
    },
    checkUserInputBet() {
      let renxuanCounts = 0;
      for (let i = 0; i < this.checkboxType.length; i++) {
        if (this.checkboxType[i].selected === true) {
          renxuanCounts = renxuanCounts + 1;
        }
      }
      const result = checkLotteryCounts({
        betData: this.userInputBet,
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"],
        renxuanCounts: renxuanCounts
      });
      result.labelInfo = result.numberStr;
      this.selectedCounts = result.betCount;
      this.$store.commit(SET_SSC_SELECTED_COUNTS, this.selectedCounts);
      return result;
    },
    randomInputCounts(counts) {
      this.userInputBet = checkLotteryCounts({
        betData: "",
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"],
        isCreateByRandom: counts
      }).numberStr;
      this.checkUserInputBet();
    }
  },
  watch: {
    flagCount() {
      const result = this.checkUserInputBet();
      if (result.result !== 0) {
        return this.getShoppingCarList(false, false, 0);
      }
      this.getShoppingCarList(
        result.numberStr,
        this.selectedCounts,
        this.getExtraStr(),
        result.labelInfo
      );
    }
  },
  created() {
    console.log(this.nowActiveWayInfoDetail);
  }
};
</script>

<style scoped>
@import "../statics/css/plate.css";
@import "../statics/css/label-repeat.css";
</style>
